<template>
  <div class="footer">
    <div class="footer_left_control">
      <a href="javascript:;" class="btn_prev sprite_player" title="alt+←" @click="prevMusic()"></a>
      <a href="javascript:;" class="btn_play sprite_player" title="[空格]"
         :class="{btn_play_pause: musicData.isMusicPlay}"
         @click="playOrPauseMusic()"></a>
      <a href="javascript:;" class="btn_next sprite_player" title="alt+→" @click="nextMusic()"></a>
    </div>

    <div class="footer_middle">
      <div class="footer_middle_info">
        <a href="javascript:;" class="footer_music_name"></a>
        {{musicName}} - {{musicSinger}}
        <a href="javascript:;" class="footer_singer_name"></a>
      </div>
      <div class="footer_middle_time">{{musicData.musicCurrentTime}} / {{musicTotalTime}}</div>
      <progress-line
        v-bind="{percentIn: musicData.musicCurrentProgress,
                        loadRatio: musicData.musicLoadRatio,
                        isRealTime: false}"
        @change-progress="updateProgressVal">
      </progress-line>
    </div>

    <div class="footer_right_control">
      <a href="javascript:;" class="btn_style sprite_player"></a
      ><a href="javascript:;" class="btn_like sprite_player"></a
      ><a href="javascript:;" class="btn_down sprite_player"></a
      ><a href="javascript:;" class="btn_comment sprite_player">
        <span class="comment_numbers">
            <i class="comment_num_9 sprite_comment"></i
            ><i class="comment_num_9 sprite_comment"></i
        ><i class="comment_num_9 sprite_comment"></i
        ><i class="comment_num_add sprite_comment"></i>
        </span>
      </a
      ><a href="javascript:;" class="btn_mode sprite_player"></a
      ><div class="player_voice">
        <a href="javascript:;" class="btn_voice sprite_player"></a>
        <div class="voice_progress">
          <progress-line
            v-bind="{percentIn: musicData.audioVolume}"
            @change-progress="updateVolume">
          </progress-line>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  import ProgressLine from "./ProgressLine";

  export default {
    name: "PlayerFooter",
    data: function () {
      return {
        musicData: {
          musicCurrentTime: '00:00',
          musicCurrentProgress: 0,
          musicLoadRatio: 0,
          audio: {},
          audioVolume: 0,
          musicList: [],
          currentMusic: {},
          currentMusicIndex: 0,
          isMusicPlay: false
        }
      }
    },
    watch: {
      isMusicDataNeedUpdate: function() {
        if(this.isMusicDataNeedUpdate) {
          this.updateDataFromStore();
          this.$store.commit('musicDataNeedUpdate', false);
        }
      }
    },
    methods: {
      updateDataFromStore() {
        const store = this.$store.state.musicData;
        //更新歌曲列表
        if(this.musicData.musicList !== store.musicList) {
          this.musicData.musicList = store.musicList;
        }
        //切换当前歌曲
        if(this.musicData.currentMusicIndex !== store.currentMusicIndex) {
          this.musicData.currentMusicIndex = store.currentMusicIndex;
          this.changeMusicByIndex();
        }
        //切换播放状态：播放/暂停
        if(this.musicData.isMusicPlay !== store.isMusicPlay) {
          this.musicData.isMusicPlay = store.isMusicPlay;
        }
        this.playMusic(this.musicData.isMusicPlay);
        // console.log('music data has synchronized from store');
      },
      updateDataToStore(syncDataName) {
        this.$store.commit("synchronizeMusicData",
            {prop: syncDataName, val: this.musicData[syncDataName]});
        // console.log(syncDataName + ' has synchronized to store');
      },
      changeMusicByIndex(newMusicIndex) {
        if(newMusicIndex) {
          this.musicData.currentMusicIndex = newMusicIndex;
        }
        this.musicData.currentMusic = this.musicData.musicList[this.musicData.currentMusicIndex];
        this.musicData.audio.src = this.musicData.currentMusic.link_url;
        this.musicData.musicCurrentTime = '00:00';
        this.musicData.musicCurrentProgress = 0;
        this.musicData.musicLoadRatio = 0;
        this.getCurrentMusicLyric();
        this.updateDataToStore('currentMusicIndex');
        this.updateDataToStore('currentMusic');
      },
      getCurrentMusicLyric() {
        const vm = this;
        this.musicData.currentMusic.lyric = [];
        this.musicData.currentMusic.times = [];
        axios({
          method:'get',
          url: this.musicData.currentMusic.link_lrc,
          responseType:'text'
        }).then(function(response) {
          const lyricArray = response.data.split("\r\n");
          lyricArray.forEach(function (item, index) {
            //歌词
            const lrc = item.split("]")[1];
            if(!lrc.length) return;
            vm.musicData.currentMusic.lyric.push(lrc);

            //每条歌词对应的时间
            const timeString = item.match(/\[(\d*:\d*\.\d*)\]/);
            if(timeString == null) return;
            const minAndSec = timeString[1].split(":");
            const min = parseInt(minAndSec[0]) * 60;
            const sec = parseFloat(minAndSec[1]);
            const time = (min + sec).toFixed(2);
            vm.musicData.currentMusic.times.push(time);
          });
        }, response => {
          console.log("error" + response);
        });
      },
      playMusic(newState) {
        if(newState) {
          this.musicData.audio.play();
          this.musicData.isMusicPlay = true;
        }
        else {
          this.musicData.audio.pause();
          this.musicData.isMusicPlay = false;
        }
        this.updateDataToStore('isMusicPlay');
      },
      playOrPauseMusic() {
        if(this.musicData.audio.src.length === 0) {
          this.changeMusicByIndex();
        }
        if(this.musicData.isMusicPlay) {
          this.playMusic(false);
        }
        else {
          this.playMusic(true);
        }
      },
      nextMusic() {
        this.musicData.currentMusicIndex += 1;
        if(this.musicData.currentMusicIndex > this.musicData.musicList.length - 1) {
          this.musicData.currentMusicIndex = 0;
        }
        this.changeMusicByIndex();
        this.playMusic(true);
      },
      prevMusic() {
        this.musicData.currentMusicIndex -= 1;
        if(this.musicData.currentMusicIndex < 0) {
          this.musicData.currentMusicIndex = this.musicData.musicList.length - 1;
        }
        this.changeMusicByIndex();
        this.playMusic(true);
      },
      computeCurrentTime(vm) {
        vm.$store.commit('synchronizeMusicData',
            {prop: 'musicCurrentTime', val: vm.musicData.audio.currentTime});
        let currentMin = (vm.musicData.audio.currentTime / 60).toFixed(0);
        let currentSec = (vm.musicData.audio.currentTime % 60).toFixed(0);
        if(currentMin < 10){
          currentMin = "0" + currentMin;
        }
        if(currentSec < 10){
          currentSec = "0" + currentSec;
        }
        vm.musicData.musicCurrentTime = currentMin + ":" + currentSec;
        vm.musicData.musicCurrentProgress = vm.musicData.audio.currentTime / vm.musicData.audio.duration;
        vm.musicData.musicLoadRatio = vm.musicData.audio.buffered.end(0) / vm.musicData.audio.duration;
      },
      updateProgressVal(progressRatio) {
        this.musicData.audio.currentTime = this.musicData.audio.duration * progressRatio;
        this.computeCurrentTime(this);
      },
      updateVolume(progressRatio) {
        this.musicData.audioVolume = this.musicData.audio.volume = progressRatio;
      }
    },
    computed: {
      isMusicDataNeedUpdate() {
        return this.$store.state.musicData.isMusicDataNeedUpdate;
      },
      musicName() {
        return this.musicData.currentMusic.name;
      },
      musicSinger() {
        return this.musicData.currentMusic.singer;
      },
      musicTotalTime() {
        return this.musicData.currentMusic.time;
      }
    },
    created() {
      this.musicData.audio = new Audio();
      this.musicData.audioVolume = this.musicData.audio.volume = 0.1;
      const vm = this;
      setInterval(function () {
        vm.computeCurrentTime(vm);
      },300);

      //加载歌曲列表
      axios({
        method:'get',
        url: '/musiclist.json',
        responseType:'json'
      }).then(response => {
        response.data.forEach(function (item, index) {
          const musicItem = item;
          musicItem.index = index + 1;
          vm.musicData.musicList.push(musicItem);
        });
        vm.changeMusicByIndex();
        vm.updateDataToStore('musicList');
        vm.$store.commit('initTotalCheckNum', vm.musicData.musicList.length);
      }, response => {
        console.log("error" + response);
      });
    },
    components: {
      ProgressLine
    }
  }
</script>

<style scoped>
