<template>
  <div class="song_info_lyric">
    <div class="song_info_lyric_inner"
         :style="{transform: `translateY(${this.currentLyricIndex * -30}px)`}">
      <p class="lyric" v-for="(item, index) in lyric">{{item}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "PlayerMusicLyric",
    data: function() {
      return {
        lyric: [],
        currentLyricIndex: 0,
      }
    },
    watch: {
      storeLyric: function () {
        this.lyric = this.storeLyric;
      },
      musicCurrentTime: function () {
        this.translateLyric(this.musicCurrentTime);
      }
    },
    methods: {
      translateLyric(currentTime) {
        const vm = this;
        this.currentLyricIndex = this.storeLyricTimes.findIndex(function (item, index) {
          if (index < vm.storeLyricTimes.length
              && currentTime >= vm.storeLyricTimes[index]
              && currentTime < vm.storeLyricTimes[index + 1]) {
            return true;
          }
          else if(index === vm.storeLyricTimes.length
              && currentTime >= vm.storeLyricTimes[index - 1]
              && currentTime < vm.storeLyricTimes[index]) {
            return true;
          }
          else {
            return false;
          }
        });
        if(this.currentLyricIndex < 0) {
          this.currentLyricIndex = 0;
        }
        document.querySelectorAll('.lyric').forEach(function (item) {
          item.classList.remove('on');
        })
        document.querySelectorAll('.lyric')[this.currentLyricIndex].classList.add('on');
      }
    },
    computed: {
      storeLyric() {
        const lyric = this.$store.state.musicData.currentMusic.lyric;
        if(lyric) {
          return lyric;
        }
      },
      musicCurrentTime() {
        return this.$store.state.musicData.musicCurrentTime;
      },
      storeLyricTimes() {
        return this.$store.state.musicData.currentMusic.times;
      }
    }
  }
</script>

<style scoped>
  .song_info_lyric{
    width: 300px;
    height: 70px;
    margin: 15px auto 0 auto;
    overflow: hidden;
    -webkit-mask-image: linear-gradient(to bottom,rgba(255,255,255,0) 0,rgba(255,255,255,.6) 15%,
    rgba(255,255,255,1) 25%,rgba(255,255,255,1) 75%,
    rgba(255,255,255,.6) 85%,rgba(255,255,255,0) 100%);
  }

  .song_info_lyric_inner{
    margin-top: 20px;
    line-height: 30px;
    transition: transform .5s;
  }

  .song_info_lyric_inner > p{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .song_info_lyric_inner > p.on{
    color: #31c27c;
  }
</style>